ain .send-email-container .send-email{margin:1rem;padding:1.2rem 1rem}body main .subtext{font-weight:100;margin-top:1rem;font-style:italic}body main h1{font-weight:600}body main img{margin-bottom:1rem;margin-top:0rem;height:10rem}body .container{width:100%}body .container .logo{font-size:1rem;margin:0;display:grid}body nav input{position:absolute;left:2rem;top:2rem;height:2.5rem;width:2rem;z-index:2;opacity:0}body nav input:checked ~ ul{-webkit-transform:none;transform:none}body nav input:checked ~ div>div:nth-child(1){background-color:#043af9;-webkit-transform:rotate(130deg);transform:rotate(130deg);width:40px;-webkit-transform-origin:18px 7px;transform-origin:18px 7px}body nav input:checked ~ div>div:nth-child(2){display:none}body nav input:checked ~ div>div:nth-child(3){background-color:#043af9;-webkit-transform:rotate(-130deg);transform:rotate(-130deg);width:40px;-webkit-transform-origin:18px -3px;transform-origin:18px -3px}body nav .hamburger{cursor:pointer;position:fixed;left:2rem;top:2rem;display:grid;width:50px;height:40px}body nav .hamburger .line{width:35px;height:3px;margin:3px;transition:-webkit-transform 0.2s cubic-bezier(0.58, 0.3, 1, 0.28);transition:transform 0.2s cubic-bezier(0.58, 0.3, 1, 0.28);transition:transform 0.2s cubic-bezier(0.58, 0.3, 1, 0.28), -webkit-transform 0.2s cubic-bezier(0.58, 0.3, 1, 0.28)}body nav .nav-container{z-index:1;position:fixed;top:5rem;left:0;-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0);min-width:100%;height:85vh;display:grid;grid-template-rows:repeat(5, 1fr);grid-template-columns:1fr;background-color:#333;transition:-webkit-transform 0.5s cubic-bezier(0.58, 0.3, 1, 0.28);transition:transform 0.5s cubic-bezier(0.58, 0.3, 1, 0.28);transition:transform 0.5s cubic-bezier(0.58, 0.3, 1, 0.28), -webkit-transform 0.5s cubic-bezier(0.58, 0.3, 1, 0.28)}body nav .nav-container li{padding:0;transition:-webkit-transform 0.1s ease-in-out;transition:transform 0.1s ease-in-out;transition:transform 0.1s ease-in-